import React, { Component } from 'react'
import beepDone from '../beepDone.mp3'

const ConstantTimeCountdown = ({ constantInterval, constantDurationState, countdown, breakTime, changeDisplay, breakRoundCounter, intervals }) => {
	let title = changeDisplay()
	let timeDisplay = constantDurationState
	if (title === "Seconds left for break") {
		timeDisplay = breakRoundCounter
	}
	if (isNaN(timeDisplay)) {
		timeDisplay = 'Done'
	}
	
	let roundCounter = intervals - constantInterval + 1

	if (roundCounter > intervals) {
		roundCounter = intervals
		/*
		var sound = new Audio(beepDone)
		sound.play()
		console.log('done')
		*/
	}
	
	return (
		<div className="tc">
			<h1 style={{display: "inline"}}>Begin Meditating &nbsp; &nbsp; | &nbsp; &nbsp; </h1>
			<h2 style={{display: "inline"}}>Rounds left: </h2>
			<h2 style={{display: "inline"}}>{roundCounter}/{intervals}  &nbsp; &nbsp; | &nbsp; &nbsp; </h2>
			<h2 style={{display: "inline"}}>{title}: </h2>
			<h2 style={{display: "inline"}}>{timeDisplay}</h2>
		</div>
	)
}

export default ConstantTimeCountdown